@import url("https://fonts.googleapis.com/css2?family=Afacad+Flux:wght@100..1000&display=swap");

* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

/* defining some css variables */

:root {
  --color-bg: #023047;
  --color-bg-variant: #219ebc;
  --color-primary: #8ecae6;
  --color-primary-variant: rgba(77, 181, 255, 0.4);
  --color-white: #fff;
  --color-light: rgba(255, 255, 255, 0.6);

  --container-width-lg: 70%;
  --container-width-md: 86%;
  --container-width-sm: 90%;

  --transition: all 400ms ease;
}

html {
  scroll-behavior: smooth;
}

/* removing the scrollbar */

::-webkit-scrollbar {
  display: none;
}

body {
  font-family: "Afacad Flux", sans-serif;
  background: var(--color-bg);
  color: var(--color-white);
  line-height: 1.7;
  background: hsla(200, 95%, 14%, 1);

  background: radial-gradient(
    circle,
    hsla(200, 95%, 14%, 1) 0%,
    hsla(192, 70%, 43%, 1) 100%
  );

  background: -moz-radial-gradient(
    circle,
    hsla(200, 95%, 14%, 1) 0%,
    hsla(192, 70%, 43%, 1) 100%
  );

  background: -webkit-radial-gradient(
    circle,
    hsla(200, 95%, 14%, 1) 0%,
    hsla(192, 70%, 43%, 1) 100%
  );

  filter: progid: DXImageTransform.Microsoft.gradient( startColorstr="#023047", endColorstr="#219EBC", GradientType=1 );
}

/* General Styles */

.container {
  width: var(--container-width-lg);
  margin: 0 auto; /*position in the middle horizontally */
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 500;
}

h1 {
  font-size: 2.5rem;
}

section {
  margin-top: 8rem; /*Having some white space around different section*/
}

section > h2,
section > h5 {
  text-align: center;
  color: var(--color-light);
}

section > h2 {
  color: var(--color-primary);
  margin-bottom: 3rem;
}

.text-light {
  color: var(--color-light);
}

a {
  color: var(--color-primary);
  transition: var(--transition);
}

a:hover {
  color: var(--color-white);
}

.btn {
  width: max-content; /* fit exactly it's content */
  display: inline-block;
  color: var(--color-primary);
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid var(--color-primary);
  transition: var(--transition);
}

.btn:hover {
  background: var(--color-white);
  color: var(--color-bg);
  border-color: transparent;
}

.btn-primary {
  background: var(--color-primary);
  color: var(--color-bg);
}

img {
  display: block;
  width: 100%;
  object-fit: cover;
}

/* Media queries for medium devices(Tablets) */

@media screen and (max-width: 1024px) {
  .container {
    width: var(--container-width-md);
  }

  section {
    margin-top: 6rem;
  }
}

/* Media queries for small devices(Mobiles) */

@media screen and (max-width: 600px) {
  .container {
    width: var(--container-width-sm);
  }

  section > h2 {
    margin-bottom: 2rem;
  }
}
